.workflow-widget .btn {
  align-items: center;
  background-color: transparent;
  border: 0;
  border-radius: 3px;
  display: flex;
  padding: 0 5px 0 2px;
}

.workflow-widget .btn svg {
  margin-right: 5px;
  width: 20px;
}

.workflow-widget__item {
  font-size: 1.05rem;
  padding-left: 14px;
  padding-right: 14px;
  transition: color 150ms, background-color 150ms;
}

.workflow-widget__item.workflow-widget__item--active {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.workflow-widget__item.workflow-widget__item--active span {
  display: flex;
  font-size: 22px;
  margin-left: 20px;
}

.workflow-widget__item.workflow-widget__item--no-state {
  color: #566b8d;
  font-style: italic;
}

.workflow-widget__item:hover,
.workflow-widget__item:focus,
.workflow-widget__item:active {
  background-color: #566b8d;
  color: white;
}




